// Sistema de Cache de Sesso Otimizado
// Mantm a sesso do usurio localmente para evitar deslogamentos desnecessrios

interface SessionData {
  user: any
  access_token: string
  refresh_token: string
  expires_at: number
  created_at: number
}

class SessionCache {
  private static instance: SessionCache
  private readonly SESSION_KEY = 'sb-mywaoaofatgwbbtyqfpd-auth-token'
  private readonly BACKUP_KEY = 'repovagas-session-backup'
  private readonly MAX_AGE = 7 * 24 * 60 * 60 * 1000 // 7 dias para manter logado

  static getInstance(): SessionCache {
    if (!SessionCache.instance) {
      SessionCache.instance = new SessionCache()
    }
    return SessionCache.instance
  }

  // Salvar sesso
  saveSession(sessionData: SessionData): void {
    try {
      // Verificar se localStorage est disponvel
      if (typeof window === 'undefined' || !window.localStorage) {
        console.log(' localStorage no disponvel para salvar sesso')
        return
      }

      const session = {
        ...sessionData,
        created_at: Date.now(),
        expires_at: sessionData.expires_at || (Date.now() + this.MAX_AGE)
      }

      localStorage.setItem(this.SESSION_KEY, JSON.stringify(session))
      localStorage.setItem(this.BACKUP_KEY, JSON.stringify(session))
      
      console.log(' Sesso salva no cache local')
    } catch (error) {
      console.warn(' Erro ao salvar sesso no cache:', error)
    }
  }

  // Recuperar sesso
  getSession(): SessionData | null {
    try {
      // Verificar se localStorage est disponvel
      if (typeof window === 'undefined' || !window.localStorage) {
        console.log(' localStorage no disponvel')
      return null
    }

      // Tentar chave principal primeiro
      let sessionData = this.getSessionFromKey(this.SESSION_KEY)
      
      // Se no encontrar, tentar backup
      if (!sessionData) {
        sessionData = this.getSessionFromKey(this.BACKUP_KEY)
      }

      if (sessionData) {
        // Verificar se no expirou
        if (this.isSessionValid(sessionData)) {
          console.log(' Sesso vlida encontrada no cache')
          return sessionData
        } else {
          console.log(' Sesso expirada, mas mantendo para evitar deslogamento')
          // NO remover sesso automaticamente - deixar para o usurio fazer logout manual
          return sessionData
        }
      }

      return null
    } catch (error) {
      console.warn(' Erro ao recuperar sesso do cache:', error)
      return null
    }
  }

  // Verificar se sesso  vlida (mais tolerante)
  isSessionValid(sessionData: SessionData): boolean {
    if (!sessionData) return false
    
    const now = Date.now()
    const expiresAt = sessionData.expires_at || (sessionData.created_at + this.MAX_AGE)
    
    // Dar uma margem de 24 horas para evitar expiraes desnecessrias
    const gracePeriod = 24 * 60 * 60 * 1000 // 24 horas
    const isValid = now < (expiresAt + gracePeriod)
    
    if (!isValid) {
      console.log(' Sesso expirada:', {
        now: new Date(now).toISOString(),
        expiresAt: new Date(expiresAt).toISOString(),
        gracePeriod: gracePeriod / (60 * 60 * 1000) + 'h'
      })
    }
    
    return isValid
  }

  // Limpar sesso
  clearSession(): void {
    try {
      localStorage.removeItem(this.SESSION_KEY)
      localStorage.removeItem(this.BACKUP_KEY)
      console.log(' Sesso removida do cache')
    } catch (error) {
      console.warn(' Erro ao limpar sesso do cache:', error)
    }
  }

  // Verificar se h sesso vlida
  hasValidSession(): boolean {
    const session = this.getSession()
    return session !== null && this.isSessionValid(session)
  }

  // Obter sesso mesmo que "expirada" (para casos de emergncia)
  getSessionForce(): SessionData | null {
    try {
      // Verificar se localStorage est disponvel
      if (typeof window === 'undefined' || !window.localStorage) {
        console.log(' localStorage no disponvel')
        return null
      }

      // Tentar chave principal primeiro
      let sessionData = this.getSessionFromKey(this.SESSION_KEY)
      
      // Se no encontrar, tentar backup
      if (!sessionData) {
        sessionData = this.getSessionFromKey(this.BACKUP_KEY)
      }

      if (sessionData) {
        console.log(' Sesso encontrada (forada) no cache - mantendo usurio logado')
        return sessionData
      }

      return null
    } catch (error) {
      console.warn(' Erro ao recuperar sesso forada do cache:', error)
      return null
    }
  }

  // Mtodo para sempre manter sesso (ultra agressivo)
  getSessionAlways(): SessionData | null {
    try {
      // Verificar se localStorage est disponvel
      if (typeof window === 'undefined' || !window.localStorage) {
        console.log(' localStorage no disponvel')
        return null
      }

      // Tentar chave principal primeiro
      let sessionData = this.getSessionFromKey(this.SESSION_KEY)
      
      // Se no encontrar, tentar backup
      if (!sessionData) {
        sessionData = this.getSessionFromKey(this.BACKUP_KEY)
      }

      if (sessionData) {
        console.log(' Sesso encontrada (sempre) no cache - SEMPRE mantendo usurio logado')
        return sessionData
      }

      return null
    } catch (error) {
      console.warn(' Erro ao recuperar sesso sempre do cache:', error)
      return null
    }
  }

  // Obter dados da sesso de uma chave especfica
  private getSessionFromKey(key: string): SessionData | null {
    try {
      const sessionStr = localStorage.getItem(key)
      if (!sessionStr) return null
      
      return JSON.parse(sessionStr) as SessionData
    } catch (error) {
      console.warn(` Erro ao parsear sesso da chave ${key}:`, error)
      return null
    }
  }

  // Atualizar apenas o token de acesso
  updateAccessToken(newToken: string): void {
    try {
      const session = this.getSession()
      if (session) {
        session.access_token = newToken
        this.saveSession(session)
        console.log(' Token de acesso atualizado no cache')
      }
    } catch (error) {
      console.warn(' Erro ao atualizar token de acesso:', error)
    }
  }

  // Obter estatsticas do cache
  getStats(): { hasSession: boolean; isValid: boolean; expiresAt?: number } {
    const session = this.getSession()
    return {
      hasSession: session !== null,
      isValid: session !== null && this.isSessionValid(session),
      expiresAt: session?.expires_at
    }
  }
}

// Singleton export
export const sessionCache = SessionCache.getInstance()

// Hook para usar o cache de sesso
export function useSessionCache() {
  return {
    saveSession: sessionCache.saveSession.bind(sessionCache),
    getSession: sessionCache.getSession.bind(sessionCache),
    getSessionForce: sessionCache.getSessionForce.bind(sessionCache),
    getSessionAlways: sessionCache.getSessionAlways.bind(sessionCache),
    clearSession: sessionCache.clearSession.bind(sessionCache),
    hasValidSession: sessionCache.hasValidSession.bind(sessionCache),
    updateAccessToken: sessionCache.updateAccessToken.bind(sessionCache),
    getStats: sessionCache.getStats.bind(sessionCache)
  }
}