export const APP_VERSION = "1.0.6"
export const BUILD_DATE = new Date().toISOString()

// Chave para armazenar a verso atual no localStorage
const VERSION_STORAGE_KEY = 'repositoriodevagas_current_version'
const LAST_CHECK_KEY = 'repositoriodevagas_last_check'

interface VersionInfo {
  version: string
  buildDate: string
  description?: string
}

// Funo para obter a verso atual armazenada
export const getCurrentStoredVersion = (): string | null => {
  try {
    return localStorage.getItem(VERSION_STORAGE_KEY)
  } catch {
    return null
  }
}

// Funo para armazenar a verso atual
export const setCurrentStoredVersion = (version: string): void => {
  try {
    localStorage.setItem(VERSION_STORAGE_KEY, version)
    localStorage.setItem(LAST_CHECK_KEY, new Date().toISOString())
  } catch (error) {
    console.warn('No foi possvel armazenar a verso:', error)
  }
}

// Funo para buscar informaes da verso do servidor
export const fetchServerVersion = async (): Promise<VersionInfo | null> => {
  try {
    // Adicionar timestamp para evitar cache
    const timestamp = Date.now()
    const response = await fetch(`/version.json?t=${timestamp}`, {
      method: 'GET',
      cache: 'no-store',
      headers: {
        'Cache-Control': 'no-store, no-cache, must-revalidate, proxy-revalidate',
        'Pragma': 'no-cache',
        'Expires': '0'
      }
    })
    
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`)
    }
    
    const data = await response.json()
    
    if (!data.version) {
      throw new Error('Verso no encontrada na resposta do servidor')
    }
    
    console.log(' Verso do servidor:', data.version, '| Verso local:', APP_VERSION)
    return data
  } catch (error) {
    console.error(' Erro ao buscar verso do servidor:', error)
    return null
  }
}

// Funo para verificar se h nova verso disponvel
export const checkForUpdates = async (): Promise<boolean> => {
  try {
    console.log(' Verificando atualizaes...')
    
    // Verificar se j verificamos recentemente (evitar spam)
    const lastCheck = localStorage.getItem(LAST_CHECK_KEY)
    if (lastCheck) {
      const lastCheckTime = new Date(lastCheck).getTime()
      const now = Date.now()
      const timeDiff = now - lastCheckTime
      
      // Se verificamos h menos de 5 minutos, no verificar novamente
      if (timeDiff < 5 * 60 * 1000) {
        console.log(' Verificao muito recente, pulando...')
        return false
      }
    }
    
    // Buscar verso do servidor
    const serverVersion = await fetchServerVersion()
    
    if (!serverVersion) {
      console.log(' No foi possvel obter verso do servidor')
      return false
    }
    
    // Obter verso armazenada localmente
    const storedVersion = getCurrentStoredVersion()
    
    // Se no h verso armazenada, armazenar a atual e no mostrar atualizao
    if (!storedVersion) {
      console.log(' Primeira verificao, armazenando verso atual')
      setCurrentStoredVersion(APP_VERSION)
      return false
    }
    
    // Comparar verses - CORREO: comparar com APP_VERSION atual, no com storedVersion
    const hasUpdate = serverVersion.version !== APP_VERSION
    
    console.log(' Comparao de verses:')
    console.log(`   APP_VERSION atual: ${APP_VERSION}`)
    console.log(`   Servidor: ${serverVersion.version}`)
    console.log(`   Armazenada: ${storedVersion}`)
    console.log(`   Nova verso disponvel: ${hasUpdate ? ' SIM' : ' NO'}`)
    
    // Atualizar timestamp da ltima verificao
    localStorage.setItem(LAST_CHECK_KEY, new Date().toISOString())
    
    return hasUpdate
  } catch (error) {
    console.error(' Erro ao verificar atualizaes:', error)
    return false
  }
}

// Funo para forar reload da aplicao
export const forceReload = () => {
  console.log(' Forando reload da aplicao...')
  
  // Buscar a verso atual do servidor e atualizar o localStorage antes do reload
  fetchServerVersion().then(serverVersion => {
    if (serverVersion) {
      // CORREO: Atualizar para a verso do servidor, no manter a atual
      setCurrentStoredVersion(serverVersion.version)
      console.log(' Verso atualizada no localStorage para:', serverVersion.version)
      
      // Limpar cache do navegador para garantir que a nova verso seja carregada
      if ('caches' in window) {
        caches.keys().then(names => {
          names.forEach(name => {
            caches.delete(name)
          })
          console.log(' Cache limpo para nova verso')
        })
      }
    }
  }).catch(error => {
    console.warn(' No foi possvel atualizar verso antes do reload:', error)
  }).finally(() => {
    // Forar reload com cache bypass para garantir nova verso
    setTimeout(() => {
      window.location.reload() // Fora reload sem cache
    }, 100)
  })
}

// Funo para inicializar o sistema de verso (chamada na inicializao da app)
export const initializeVersionSystem = () => {
  try {
    const storedVersion = getCurrentStoredVersion()
    
    if (!storedVersion) {
      // Primeira vez que a aplicao  executada
      setCurrentStoredVersion(APP_VERSION)
      console.log(' Sistema de verso inicializado com verso:', APP_VERSION)
    } else {
      console.log(' Verso armazenada:', storedVersion, '| Verso atual:', APP_VERSION)
    }
  } catch (error) {
    console.warn(' Erro ao inicializar sistema de verso:', error)
  }
}
