import { useState, useEffect, useCallback, useRef } from 'react'
import { checkForUpdates, forceReload, getCurrentStoredVersion, setCurrentStoredVersion } from '../version'

interface UseUpdateCheckOptions {
  checkOnMount?: boolean
  checkInterval?: number // em milissegundos
  showModalDelay?: number // delay antes de mostrar o modal
  autoCheckOnFocus?: boolean // verificar quando a janela ganha foco
}

export function useUpdateCheck(options: UseUpdateCheckOptions = {}) {
  const {
    checkOnMount = true,
    checkInterval = 0, // 0 = no verificar automaticamente
    showModalDelay = 2000, // 2 segundos de delay
    autoCheckOnFocus = true // verificar quando a janela ganha foco
  } = options

  const [hasUpdate, setHasUpdate] = useState(false)
  const [isChecking, setIsChecking] = useState(false)
  const [showModal, setShowModal] = useState(false)
  const [lastChecked, setLastChecked] = useState<Date | null>(null)
  const [serverVersion, setServerVersion] = useState<string | null>(null)
  
  // Ref para evitar mltiplas verificaes simultneas
  const isCheckingRef = useRef(false)

  const checkForUpdatesNow = useCallback(async (forceShowModal = false) => {
    // Evitar mltiplas verificaes simultneas
    if (isCheckingRef.current) {
      console.log(' Verificao j em andamento, aguardando...')
      return false
    }

    isCheckingRef.current = true
    setIsChecking(true)
    
    try {
      console.log(' Iniciando verificao de atualizaes...')
      const hasNewVersion = await checkForUpdates()
      
      setHasUpdate(hasNewVersion)
      setLastChecked(new Date())
      
      // Se h atualizao, mostrar modal
      if (hasNewVersion && (forceShowModal || !showModal)) {
        console.log(' Nova verso encontrada, mostrando modal em', showModalDelay, 'ms')
        setTimeout(() => {
          setShowModal(true)
        }, showModalDelay)
      }
      
      console.log(' Verificao concluda. Nova verso:', hasNewVersion ? 'SIM' : 'NO')
      return hasNewVersion
    } catch (error) {
      console.error(' Erro ao verificar atualizaes:', error)
      return false
    } finally {
      setIsChecking(false)
      isCheckingRef.current = false
    }
  }, [showModal, showModalDelay])

  const handleUpdate = useCallback(() => {
    console.log(' Iniciando atualizao da aplicao...')
    forceReload()
  }, [])

  const handleCloseModal = useCallback(() => {
    console.log(' Modal de atualizao fechado pelo usurio')
    setShowModal(false)
  }, [])

  // Verificar atualizaes na montagem do componente
  useEffect(() => {
    if (checkOnMount) {
      // Pequeno delay para garantir que a aplicao esteja carregada
      const timer = setTimeout(() => {
        checkForUpdatesNow()
      }, 1000)
      
      return () => clearTimeout(timer)
    }
  }, [checkOnMount, checkForUpdatesNow])

  // Verificar atualizaes em intervalos regulares (se configurado)
  useEffect(() => {
    if (checkInterval > 0) {
      console.log(` Configurando verificao automtica a cada ${checkInterval}ms`)
      const interval = setInterval(() => {
        checkForUpdatesNow()
      }, checkInterval)
      return () => {
        console.log(' Parando verificao automtica')
        clearInterval(interval)
      }
    }
  }, [checkInterval, checkForUpdatesNow])

  // Verificar atualizaes quando a janela ganha foco (se configurado)
  useEffect(() => {
    if (!autoCheckOnFocus) return

    const handleFocus = () => {
      console.log(' Janela ganhou foco, verificando atualizaes...')
      // Pequeno delay para evitar verificaes muito frequentes
      setTimeout(() => {
        checkForUpdatesNow()
      }, 500)
    }

    const handleVisibilityChange = () => {
      if (!document.hidden) {
        console.log(' Pgina ficou visvel, verificando atualizaes...')
        setTimeout(() => {
          checkForUpdatesNow()
        }, 500)
      }
    }

    window.addEventListener('focus', handleFocus)
    document.addEventListener('visibilitychange', handleVisibilityChange)

    return () => {
      window.removeEventListener('focus', handleFocus)
      document.removeEventListener('visibilitychange', handleVisibilityChange)
    }
  }, [autoCheckOnFocus, checkForUpdatesNow])

  return {
    hasUpdate,
    isChecking,
    showModal,
    lastChecked,
    serverVersion,
    checkForUpdates: checkForUpdatesNow,
    handleUpdate,
    handleCloseModal
  }
}
