import { supabase, supabaseAdmin } from './supabase'
import { User, AuthUser, LoginFormData, UserFormData } from '../types/database'
import { validatePasswordStrength, isValidPassword } from './password-utils'
import { filterVisibleUsers, SUPER_ADMIN_EMAIL } from './user-filter'

// Funo auxiliar para verificar se email existe no sistema
async function checkIfEmailExists(email: string): Promise<boolean> {
  try {
    // Verificar na tabela users
    const { data: user, error } = await supabase
      .from('users')
      .select('email')
      .eq('email', email)
      .single()
    
    return !error && !!user
  } catch (error) {
    // Se no encontrou na tabela users, retornar false
    return false
  }
}

// Funo para fazer login usando Supabase Auth
export async function signIn({ email, password }: LoginFormData): Promise<AuthUser | null> {
  try {
    // Fazer login com Supabase Auth
    const { data: authData, error: authError } = await supabase.auth.signInWithPassword({
      email,
      password
    })

    if (authError) {
      console.error('Erro de autenticao:', authError.message)
      
      // Verificar se  erro de email no confirmado
      if (authError.message.includes('Email not confirmed') || 
          authError.message.includes('email_not_confirmed') ||
          (authError.message.includes('Invalid login credentials') && 
           await checkIfEmailExists(email))) {
        throw new Error('CONFIRM_EMAIL')
      }
      
      // Outros erros de autenticao
      throw new Error(authError.message)
    }

    if (!authData.user) {
      throw new Error('Usurio no encontrado')
    }

    // Buscar dados do usurio na tabela users usando o ID do auth
    // Usar try-catch para evitar problemas de RLS
    let user = null
    try {
      const { data: userData, error: userError } = await supabase
        .from('users')
        .select('*')
        .eq('id', authData.user.id)
        .single()

      if (!userError && userData) {
        user = userData
      }
    } catch (userError) {
      console.log('Erro ao buscar usurio na tabela, usando dados do Auth:', userError)
    }

    // Se no encontrou o usurio na tabela, usar dados do Auth
    if (!user) {
      console.log('Usurio no encontrado na tabela users, usando dados do Auth')
      return {
        id: authData.user.id,
        email: authData.user.email || '',
        name: authData.user.user_metadata?.full_name || authData.user.email?.split('@')[0] || 'Usurio',
        role: authData.user.user_metadata?.role || 'RH' // Role padro
      }
    }

    // Retornar dados do usurio da tabela
    return {
      id: user.id,
      email: user.email,
      name: user.name,
      role: user.role
    }
  } catch (error) {
    console.error('Erro no login:', error)
    throw error // Re-throw para que o AuthContext possa capturar
  }
}

// Funo para criar usurio usando Supabase Auth
export async function createUser(userData: UserFormData): Promise<User | null> {
  try {
    // Verificar se temos Service Key disponvel
    const hasServiceKey = import.meta.env.VITE_SUPABASE_SERVICE_KEY && 
                        import.meta.env.VITE_SUPABASE_SERVICE_KEY !== 'your_supabase_service_role_key_here'

    if (hasServiceKey) {
      // Usar mtodo administrativo se Service Key estiver disponvel
      return await createUserWithAdmin(userData)
    } else {
      // Usar mtodo alternativo sem Service Key
      return await createUserWithoutAdmin(userData)
    }
  } catch (error) {
    console.error('Erro ao criar usurio:', error)
    throw error // Re-throw para que o componente possa capturar
  }
}

// Mtodo administrativo (requer Service Key)
async function createUserWithAdmin(userData: UserFormData): Promise<User | null> {
  try {
    // Criar usurio no Supabase Auth usando cliente administrativo
    const { data: authData, error: authError } = await supabaseAdmin.auth.admin.createUser({
      email: userData.email,
      password: userData.password,
      email_confirm: true
    })

    if (authError) {
      console.error('Erro ao criar usurio no Auth:', authError)
      throw new Error(authError.message)
    }

    if (!authData.user) {
      throw new Error('Erro ao criar usurio no Auth')
    }

    // Criar registro na tabela users usando cliente administrativo
    const { data: user, error: userError } = await supabaseAdmin
      .from('users')
      .insert({
        id: authData.user.id,
        email: userData.email,
        name: userData.name,
        role: userData.role,
        password_hash: '' // No precisamos mais do hash
      })
      .select()
      .single()

    if (userError) {
      console.error('Erro ao criar usurio na tabela:', userError)
      // Se falhou, remover o usurio do Auth
      await supabaseAdmin.auth.admin.deleteUser(authData.user.id)
      throw new Error(userError.message)
    }

    return user
  } catch (error) {
    console.error('Erro no mtodo administrativo:', error)
    throw error
  }
}

// Mtodo alternativo sem Service Key (usando signUp)
async function createUserWithoutAdmin(userData: UserFormData): Promise<User | null> {
  try {
    // Criar usurio usando signUp (mtodo pblico)
    const { data: authData, error: authError } = await supabase.auth.signUp({
      email: userData.email,
      password: userData.password,
      options: {
        data: {
          full_name: userData.name,
          role: userData.role
        }
      }
    })

    if (authError) {
      console.error('Erro ao criar usurio no Auth:', authError)
      throw new Error(authError.message)
    }

    if (!authData.user) {
      throw new Error('Erro ao criar usurio no Auth')
    }

    // Criar registro na tabela users usando cliente padro
    const { data: user, error: userError } = await supabase
      .from('users')
      .insert({
        id: authData.user.id,
        email: userData.email,
        name: userData.name,
        role: userData.role,
        password_hash: '' // No precisamos mais do hash
      })
      .select()
      .single()

    if (userError) {
      console.error('Erro ao criar usurio na tabela:', userError)
      throw new Error(userError.message)
    }

    return user
  } catch (error) {
    console.error('Erro no mtodo alternativo:', error)
    throw error
  }
}

// Funo para verificar se o usurio  admin
export function isAdmin(user: AuthUser | null): boolean {
  return user?.role === 'ADMIN'
}

// Funo para listar todos os usurios (apenas para admins)
export async function getAllUsers(): Promise<User[]> {
  try {
    const { data: users, error } = await supabase
      .from('users')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) {
      console.error('Erro ao buscar usurios:', error)
      throw new Error(error.message)
    }

    // Filtrar usurios ocultos (super admin)
    const visibleUsers = filterVisibleUsers(users || []) as User[]

    return visibleUsers
  } catch (error) {
    console.error('Erro ao listar usurios:', error)
    throw error
  }
}

// Funo para atualizar usurio
export async function updateUser(userId: string, userData: Partial<UserFormData>): Promise<User | null> {
  try {
    const updateData: any = {}
    
    if (userData.name) updateData.name = userData.name
    if (userData.email) updateData.email = userData.email
    if (userData.role) updateData.role = userData.role

    const { data: user, error } = await supabase
      .from('users')
      .update(updateData)
      .eq('id', userId)
      .select()
      .single()

    if (error) {
      console.error('Erro ao atualizar usurio:', error)
      throw new Error(error.message)
    }

    return user
  } catch (error) {
    console.error('Erro ao atualizar usurio:', error)
    throw error
  }
}

// Funo para excluir usurio
export async function deleteUser(userId: string): Promise<boolean> {
  try {
    // Verificar se temos Service Key disponvel para excluir do Auth
    const hasServiceKey = import.meta.env.VITE_SUPABASE_SERVICE_KEY && 
                        import.meta.env.VITE_SUPABASE_SERVICE_KEY !== 'your_supabase_service_role_key_here'

    if (hasServiceKey) {
      // Excluir do Supabase Auth usando cliente administrativo
      const { error: authError } = await supabaseAdmin.auth.admin.deleteUser(userId)
      if (authError) {
        console.error('Erro ao excluir usurio do Auth:', authError)
        throw new Error(authError.message)
      }
    }

    // Excluir da tabela users
    const { error: userError } = await supabase
      .from('users')
      .delete()
      .eq('id', userId)

    if (userError) {
      console.error('Erro ao excluir usurio da tabela:', userError)
      throw new Error(userError.message)
    }

    return true
  } catch (error) {
    console.error('Erro ao excluir usurio:', error)
    throw error
  }
}

