import { supabase } from './supabase'

export interface SystemConfig {
  id: string
  config_key: string
  config_value: string
  description?: string
  created_at: string
  updated_at: string
}

// Funo para obter uma configurao especfica
export async function getSystemConfig(key: string): Promise<string | null> {
  try {
    const { data, error } = await supabase
      .from('system_config')
      .select('config_value')
      .eq('config_key', key)
      .single()

    if (error) {
      console.error('Erro ao buscar configurao:', error)
      // Se a tabela no existe ou h erro de permisso, retornar valor padro
      if (error.code === 'PGRST116' || error.message.includes('relation "system_config" does not exist')) {
        console.warn('Tabela system_config no existe. Retornando valor padro.')
        return getDefaultConfigValue(key)
      }
      return null
    }

    return data?.config_value || null
  } catch (error) {
    console.error('Erro ao buscar configurao:', error)
    return getDefaultConfigValue(key)
  }
}

// Funo para obter valores padro quando a tabela no existe
function getDefaultConfigValue(key: string): string {
  const defaults: Record<string, string> = {
    'rh_nova_vaga_enabled': 'false',
    'rh_edit_enabled': 'false',
    'rh_delete_enabled': 'false'
  }
  return defaults[key] || 'false'
}

// Funo para obter todas as configuraes
export async function getAllSystemConfigs(): Promise<SystemConfig[]> {
  try {
    const { data, error } = await supabase
      .from('system_config')
      .select('*')
      .order('config_key')

    if (error) {
      throw new Error(error.message)
    }

    return data || []
  } catch (error) {
    console.error('Erro ao buscar configuraes:', error)
    return []
  }
}

// Funo para atualizar uma configurao
export async function updateSystemConfig(key: string, value: string): Promise<boolean> {
  try {
    // Primeiro, verificar se a configurao j existe
    const { data: existingData, error: selectError } = await supabase
      .from('system_config')
      .select('id')
      .eq('config_key', key)
      .single()

    if (selectError && selectError.code !== 'PGRST116') {
      console.error('Erro ao verificar configurao existente:', selectError)
    }

    let error;
    if (existingData) {
      // Se existe, fazer UPDATE
      const { error: updateError } = await supabase
        .from('system_config')
        .update({
          config_value: value,
          updated_at: new Date().toISOString()
        })
        .eq('config_key', key)
      
      error = updateError;
    } else {
      // Se no existe, fazer INSERT
      const { error: insertError } = await supabase
        .from('system_config')
        .insert({
          config_key: key,
          config_value: value,
          updated_at: new Date().toISOString()
        })
      
      error = insertError;
    }

    if (error) {
      console.error('Erro ao atualizar configurao:', error)
      // Se a tabela no existe, mostrar mensagem especfica
      if (error.code === 'PGRST116' || error.message.includes('relation "system_config" does not exist')) {
        console.error('ERRO: Tabela system_config no existe. Execute a migrao do banco de dados primeiro.')
        throw new Error('Tabela system_config no existe. Execute a migrao do banco de dados primeiro.')
      }
      throw new Error(`Erro ao atualizar configurao: ${error.message}`)
    }

    return true
  } catch (error) {
    console.error('Erro ao atualizar configurao:', error)
    throw error
  }
}

// Funo para criar configurao se no existir
export async function createSystemConfigIfNotExist(key: string, defaultValue: string, description?: string): Promise<boolean> {
  try {
    // Verificar se j existe
    const { data: existingData, error: selectError } = await supabase
      .from('system_config')
      .select('id')
      .eq('config_key', key)
      .single()

    // Se no existe erro de "no encontrado", significa que no existe
    if (selectError && selectError.code === 'PGRST116') {
      // Inserir nova configurao
      const { error: insertError } = await supabase
        .from('system_config')
        .insert({
          config_key: key,
          config_value: defaultValue,
          description: description || '',
          created_at: new Date().toISOString(),
          updated_at: new Date().toISOString()
        })

      if (insertError) {
        console.error('Erro ao criar configurao:', insertError)
        return false
      }
      
      console.log(`Configurao ${key} criada com valor padro: ${defaultValue}`)
      return true
    } else if (selectError) {
      console.error('Erro ao verificar configurao existente:', selectError)
      return false
    } else {
      // J existe, no fazer nada
      console.log(`Configurao ${key} j existe`)
      return true
    }
  } catch (error) {
    console.error('Erro ao criar configurao:', error)
    return false
  }
}

// Funo especfica para verificar se RH pode acessar Nova Oportunidade
export async function isRHNovaVagaEnabled(): Promise<boolean> {
  const value = await getSystemConfig('rh_nova_vaga_enabled')
  return value === 'true'
}

// Funo especfica para verificar se RH pode editar vagas
export async function isRHEditEnabled(): Promise<boolean> {
  const value = await getSystemConfig('rh_edit_enabled')
  return value === 'true'
}

// Funo especfica para verificar se RH pode deletar vagas
export async function isRHDeleteEnabled(): Promise<boolean> {
  const value = await getSystemConfig('rh_delete_enabled')
  return value === 'true'
}

// Funo para habilitar/desabilitar acesso RH  Nova Oportunidade
export async function setRHNovaVagaAccess(enabled: boolean): Promise<boolean> {
  return await updateSystemConfig('rh_nova_vaga_enabled', enabled.toString())
}

// Funo para habilitar/desabilitar edio RH
export async function setRHEditAccess(enabled: boolean): Promise<boolean> {
  return await updateSystemConfig('rh_edit_enabled', enabled.toString())
}

// Funo para habilitar/desabilitar excluso RH
export async function setRHDeleteAccess(enabled: boolean): Promise<boolean> {
  return await updateSystemConfig('rh_delete_enabled', enabled.toString())
}
